.blog-list {
    width: 100vw;
    background: var(--main-bg-color-1);
    padding-bottom: 10px;
}

.blog-list-item {
    width: 100%;
    height: 160px;
    margin-top: 10px;
    margin-bottom: 10px;
    cursor: pointer;
    background: var(--main-bg-color-0);
}

.blog-list-item-title-img {
    width: 100%;
    height: 100px;
    line-height: 100px;
    text-align: center;
    font-size: 30px;
    float: left;
    color: white;
    backdrop-filter: var(--main-filter);
}

.blog-list-item-text {
    width: calc(100% - 20px);
    height: 40px;
    padding: 10px;
    float: left;
    backdrop-filter: var(--main-filter);
}

.blog-list-item-title {
    width: 100%;
    line-height: 20px;
    float: left;
    font-size: 20px;
    font-weight: bold;
    display: flex;
}

.blog-list-item-title .left {
    width: 50%;
}

.blog-list-item-title .right {
    width: 50%;
    text-align: right;
    font-size: 10px;
    color: var(--primary-color);
}


.blog-list-item-subtitle {
    width: 100%;
    height: 20px;
    line-height: 20px;
    float: left;
}

.blog-user-card {
    width: 100vw;
    height: 300px;
}

.blog-user-bg {
    height: 200px;
    width: 100%;
}

.blog-user-avatar {
    position: absolute;
    top: 150px;
    display: flex;
    justify-content: center;
    width: 100%;
}

.blog-user-avatar-img {
    width: 100px;
    height: 100px;
    border-radius: 100px;
    box-shadow: var(--box-shadow);
}

.blog-user-info {
    position: absolute;
    top: 250px;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    font-weight: bold;
}

.blog-detail {
    padding-left: 10px;
    padding-right: 10px;
}

.blog-detail-title {
    height: 40px;
    line-height: 40px;
    font-size: 40px;
    font-weight: bold;
    margin: 20px 0 20px 0;
}

.blog-detail-img {
    width: 100%;
    height: 300px;
}

.blog-detail-img img {
    width: 100%;
    height: 100%;
    float: left;
    background-size: cover !important;
}

.blog-detail-text-img {
    width: 100%;
    height: 300px;
    line-height: 300px;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    color: white;
}

.blog-card {
    position: absolute;
    top: 60px;
    left: 5vw;
    right: 5vw;
    bottom: 10vh;
    box-shadow: var(--box-shadow);
    border-radius: var(--border-radius);
}

.blog-card-head {
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    height: 80px;
    line-height: 80px;
    width: 100%;
}

.blog-card-content {
    margin: 0px 10px 10px 10px;
    padding: 0px 10px 0px 10px;
    height: calc(100% - 80px - 10px);
    width: calc(100% - 20px - 20px);
    box-shadow: var(--box-shadow-active);
    overflow: auto;
    border-radius: var(--border-radius);
}

.blog-card-next-btn {
    position: fixed;
    bottom: 0;
    width: 100vw;
    height: 10vh;
    line-height: 10vh;
    text-align: center;
    cursor: pointer;
    font-weight: bold;
    font-size: 30px;
}

ol li {
    word-break: break-all;
}

.bq-operate {
    display: flex;
}

.bq-o-left {
    height: 10vh;
    width: calc(40vw - 10px);
    display: flex;
    justify-items: center;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
}

.bq-o-left .icon {
    margin: 10px;
    font-size: 30px;
}

.bq-o-right {
    width: 60vw;
    height: 10vh;
    display: flex;
    justify-items: center;
    align-items: center;
}

