/*layout*/
.header {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    height: 60px;
    line-height: 60px;
    width: 100vw;
    display: flex;
    z-index: 1;
}

.header.active {
    color: var(--main-text-color-0) !important;
    box-shadow: var(--box-shadow);
    background: var(--main-filter-nav-bg);
    backdrop-filter: var(--main-filter);
}

.header-nav-left {
    width: calc(33.3% - 5px);
    text-align: left;
    padding-left: 5px;
    cursor: pointer;
    font-size: 30px;
    font-weight: bold;
}

.header-nav-mid {
    width: 33.3%;
    text-align: center;
    font-weight: bold;
    font-size: large;
    font-weight: bold;
}

.header-nav-right {
    width: calc(33.3% - 5px);
    text-align: right;
    padding-right: 5px;
    font-size: 30px;
    cursor: pointer;
    font-weight: bold;
}

.main {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100vw;
    overflow: auto;
}

.top-main {
    position: absolute;
    top: 45px;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100vw;
    overflow: auto;
}

.bottom-main {
    position: absolute;
    top: 0;
    bottom: 10vh;
    right: 0;
    left: 0;
    width: 100vw;
    overflow: auto;
}

.top-bottom-main {
    position: absolute;
    top: 45px;
    bottom: 10vh;
    right: 0;
    left: 0;
    width: 100vw;
    overflow: auto;
}

.footer {
    position: fixed;
    bottom: 0px;
    right: 0;
    left: 0;
    width: 100vw;
    height: 10vh;
    box-shadow: var(--box-shadow);
    background: var(--main-bg-color-0);
    display: flex;
}

.footer-nav {
    width: 25%;
    text-align: center;
    font-size: 30px;
    cursor: pointer;
    font-weight: bold;
}

/*button*/
.sys-btn {
    height: 50px;
    width: calc(100% - 20px);
    line-height: 50px;
    margin: 10px;
    border-radius: var(--border-radius);
    background: var(--main-bg-color-0);
    color: var(--primary-color);
    font-weight: bold;
    text-align: center;
    cursor: pointer;
    box-shadow: var(--box-shadow);
}

.sys-pri-btn {
    height: 50px;
    width: calc(100% - 20px);
    line-height: 50px;
    margin: 10px;
    border-radius: var(--border-radius);
    background: var(--primary-color);
    color: white;
    font-weight: bold;
    text-align: center;
    cursor: pointer;
}

.sys-icon {
    font-size: 30px;
    cursor: pointer;
    background: var(--main-filter-mask-bg);
    backdrop-filter: var(--main-filter);
    box-shadow: var(--box-shadow);
    padding: 10px;
    border-radius: 30px;
}

.sys-loading {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    flex-direction: column;
    position: fixed;
    top: 25%;
    bottom: 25%;
    left: 25%;
    right: 25%;
}

.sys-loading-icon {
    animation: ac-circle 1s infinite;
    -webkit-animation: ac-circle 1s infinite;
}

.sys-popup {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100vw;
    background: var(--main-filter-mask-bg);
    backdrop-filter: var(--main-filter);
    z-index: 100;
    overflow: hidden;
}


.sys-popup-content {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100vw;
    overflow: auto;
    padding-bottom: 13vh;
}

.sys-popup-close-btn {
    position: fixed;
    bottom: 0;
    cursor: pointer;
    height: 13vh;
    line-height: 13vh;
    width: 100vw;
    text-align: center;
    font-size: 30px;
    color: red;
    font-weight: bold;
}

.sys-menu-btn {
    background: var(--main-bg-color-0);
    box-shadow: var(--box-shadow);
    border-radius: var(--border-radius);
    height: 100px;
    margin: 10px;
    width: calc(100vw - 20px);
    display: flex;
    cursor: pointer;
}

.sys-menu-btn-left {
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
    font-size: 30px;
}

.sys-menu-btn-right {
    width: calc(100% - 110px);
    height: 100px;
    line-height: 100px;
    text-align: right;
    padding-right: 10px;
    font-size: 30px;
}

.sys-input {
    font-size: 20px;
    height: 30px;
    margin: 10px;
    outline-style: none;
    padding: 10px;
    width: calc(100% - 40px);
    border: none;
    background: var(--main-bg-color-0);
    color: var(--main-text-color-0);
}

.sys-input:focus {
}

.search-head {
    position: fixed;
    top: 0px;
    backdrop-filter: var(--main-filter);
    background: var(--main-filter-mask-bg);
    width: 100vw;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
}

.search-input {
    height: 10vh;
    width: 100vw;
    display: flex;
}

.search-input input {
    font-size: 20px;
    height: calc(100% - 20px - 20px);
    margin: 10px 0px 10px 10px;
    outline-style: none;
    padding: 10px;
    width: calc(100% - 10vh - 20px);
    border: none;
    background: var(--main-bg-color-1);
    backdrop-filter: var(--main-filter);
    color: var(--main-text-color-0);
    border-radius: 0px;
}

.search-btn {
    width: calc(10vh - 20px);
    height: calc(10vh - 20px);
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--primary-color);
    margin: 10px 10px 10px 0;
    font-size: 25px;
    color: white;
    cursor: pointer;
}

.search-content {
    padding: 10vh 20px 0vh 20px;
    width: calc(100vw - 40px);
    float: left;
    overflow: auto;
}

.sys-empty {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-content: center;
}

.start-page {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: var(--main-bg-color-0);
    z-index: 100;
    display: flex;
    flex-direction: column;
    transition: 1s;
}

.start-head {
    height: 60%;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.start-foot {
    height: 40%;
    width: 100vw;
}

.start-slogan {
    position: absolute;
    bottom: 5vh;
    text-align: center;
    width: 100vw;
    font-size: 16px;
}

.sys-logo {
    background: var(--primary-color);
    color: white;
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    font-weight: bold;
    border-radius: var(--border-radius);
}

.sys-card-list {
    position: absolute;
    top: 300px;
    left: 0;
    right: 0;
    bottom: 0px;
    padding: 10px 0 10px 0;
}

.sys-card {
    background: var(--main-filter-mask-bg);
    backdrop-filter: var(--main-filter);
    border-radius: var(--border-radius);
    width: calc(100vw - 20px);
    height: 100px;
    margin: 10px;
}

@keyframes ac-circle {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

hr {
    color: var(--main-text-color-1);
}