:root:root {
    --adm-color-primary: var(--primary-color);
}

:root {
    --primary-color: #1BD1A5;
    --main-shadow-color: rgba(0, 0, 0, 0.1);
    --main-text-family: SysFont;
    --main-bg-color-0: #f2f2f2;
    --main-bg-color-1: #ffffff;
    --main-text-color-0: #000001;
    --main-text-color-1: #e7e7e7;
    --box-shadow: -2px -2px 5px var(--main-bg-color-1), 5px 5px 5px rgba(0, 0, 0, .3);
    --box-shadow-active: inset -3px -2px 2px var(--main-bg-color-1), inset 3px 3px 2px rgba(0, 0, 0, .3);
    --border-radius: 10px;
    --main-filter-mask-bg: rgba(242, 242, 242, 0.4);
    --main-filter-nav-bg: rgba(242, 242, 242, 0.85);
    --main-filter: saturate(180%) blur(12px) !important;
    --animate-duration: 500ms;
    --indexBg: url("img/lightBg.webp");
    --indexBg: linear-gradient(0deg, rgb(211, 250, 253) 0%, rgba(99, 171, 221, 1) 100%);
}

:root[theme='dark'] {
    --primary-color: #1BD1A5;
    --main-shadow-color: rgba(0, 0, 0, 0.5);
    --main-text-family: SysFont;
    --main-bg-color-0: #45494f;
    /*--main-bg-color-0: black;*/
    --main-bg-color-1: #666768;
    --main-text-color-0: white;
    --main-text-color-1: #3c3c3c;
    --box-shadow: -2px -2px 5px var(--main-bg-color-1), 5px 5px 5px rgba(0, 0, 0, .3);
    --box-shadow-active: inset -3px -2px 2px var(--main-bg-color-1), inset 3px 3px 2px rgba(0, 0, 0, .3);
    --border-radius: 10px;
    --main-filter-mask-bg: rgba(69, 73, 79, 0.4);
    --main-filter-nav-bg: rgba(69, 73, 79, 0.85);
    --main-filter: saturate(180%) blur(20px) !important;
    --main-filter: blur(10px) !important;
    --animate-duration: 500ms;
    --indexBg: url("img/darkBg.webp");
    --indexBg: linear-gradient(0deg, rgba(25, 50, 80, 1) 0%, rgba(4, 6, 22, 1) 100%);
}

@font-face {
    font-family: MiSansFont;
    src: url("font/MiSans-Normal.ttf");
}

@font-face {
    font-family: PingFang;
    src: url("font/PingFangSimple.ttf");
}

@font-face {
    font-family: SourceHanSansCN;
    src: url("font/SourceHanSansCN-Regular.otf");
}

@font-face {
    font-family: OPPOSans-R;
    src: url("font/OPPOSans-R.ttf");
}

@font-face {
    font-family: SysFont;
    src: url("font/PingFangSimple.ttf");
}

body {
    margin: 0px;
    background: var(--main-bg-color-0) !important;
    color: var(--main-text-color-0) !important;
    font-size: 15px;
}

::-webkit-scrollbar {
    display: none;
}